___CSS_LOADER_EXPORT___.push([module.id, `.add-todo-form {
  border: 1px solid black; 
}

.todo-item {
  border-bottom: 1px solid rgb(180, 180, 180); 
  padding: 10px 0px; 
}

.add-todo-button {
  display: flex; 
  align-items: center; 
  gap: 10px; 
  width: 100%; 
  height: 45px; 
  background: none; 
  border: none; 
  color: rgb(150, 150, 150); 
  cursor: pointer; 
}

.add-todo-button:hover {
  color: var(--dark-color); 
}

.add-todo-button img {
  width: 20px; 
  height: 20px; 
  border-radius: 50%;
  filter: brightness(0) saturate(100%) invert(62%) sepia(16%) saturate(18%) hue-rotate(338deg) brightness(94%) contrast(89%);
}

.add-todo-button .plus-icon {
  width: 20px; 
  height: 20px; 
  border-radius: 50%; 
}

.add-todo-button:hover .plus-icon {
  background-color: var(--dark-color);
}

.add-todo-button:hover img {
  filter: invert();
}

.todo-form {
  display: grid; 
  grid-template-rows: repeat(3, min-content); 
  grid-template-columns: 90px min-content 90px min-content 1fr repeat(2, min-content);
  gap: 5px; 
  border: 1px solid rgb(180, 180, 180); 
  border-radius: 10px;
  padding: 10px; 
  margin-top: 5px; 
}

.todo-form #title {
  grid-row: 1; 
  grid-column: 1 / -1;
  font-size: 1.1em; 
  font-weight: bold; 
  border: none; 
  color: var(--dark-color); 
}

.todo-form #title:focus {
  outline: none; 
}

.todo-form #description {
  grid-row: 2; 
  grid-column: 1 / -1;
  font-family: var(--font-family); 
  border: none; 
  resize: none; 
  color: var(--dark-color); 
}

.todo-form #description:focus {
  outline: none; 
}

.todo-form .due-date-label {
  grid-row: 3; 
  grid-column: 1; 
  color: var(--dark-color); 
  align-self: center; 
}

.todo-form #due-date {
  grid-row: 3; 
  grid-column: 2; 
  color: var(--dark-color); 
  border: rgb(180, 180, 180) 1px solid; 
  border-radius: 5px; 
  font-family: var(--font-family); 
  padding-left: 5px; 
}

.todo-form .priority-label {
  grid-row: 3; 
  grid-column: 3; 
  text-align: right;
  color: var(--dark-color); 
  align-self: center;
}

.todo-form #priority {
  grid-row: 3; 
  grid-column: 4; 
  color: var(--dark-color); 
  border: rgb(180, 180, 180) 1px solid; 
  border-radius: 5px; 
}

.todo-form .cancel, 
.todo-form .save {
  width: 70px; 
  height: 30px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer; 
}

.todo-form .cancel:hover {
  background-color: var(--light-color-shade); 
}

.todo-form .save:hover {
  background-color: var(--dark-color); 
}

.todo-form .save:disabled {
  background-color: #94af9c; 
  cursor: not-allowed; 
}

.todo-form .cancel {
  grid-row: 3; 
  grid-column: -3; 
  color: var(--dark-color); 
  background-color: var(--light-color); 
}

.todo-form .save {
  grid-row: 3; 
  grid-column: -2; 
  color: white; 
  background-color: var(--semi-dark-color); 
}`, "",{"version":3,"sources":["webpack://./src/components/todo-list/style.css"],"names":[],"mappings":"AAAA;EACE,uBAAuB;AACzB;;AAEA;EACE,2CAA2C;EAC3C,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,WAAW;EACX,YAAY;EACZ,gBAAgB;EAChB,YAAY;EACZ,yBAAyB;EACzB,eAAe;AACjB;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,0HAA0H;AAC5H;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,0CAA0C;EAC1C,mFAAmF;EACnF,QAAQ;EACR,oCAAoC;EACpC,mBAAmB;EACnB,aAAa;EACb,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,mBAAmB;EACnB,gBAAgB;EAChB,iBAAiB;EACjB,YAAY;EACZ,wBAAwB;AAC1B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,mBAAmB;EACnB,+BAA+B;EAC/B,YAAY;EACZ,YAAY;EACZ,wBAAwB;AAC1B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,cAAc;EACd,wBAAwB;EACxB,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,cAAc;EACd,wBAAwB;EACxB,oCAAoC;EACpC,kBAAkB;EAClB,+BAA+B;EAC/B,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,cAAc;EACd,iBAAiB;EACjB,wBAAwB;EACxB,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,cAAc;EACd,wBAAwB;EACxB,oCAAoC;EACpC,kBAAkB;AACpB;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,0CAA0C;AAC5C;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,yBAAyB;EACzB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,eAAe;EACf,wBAAwB;EACxB,oCAAoC;AACtC;;AAEA;EACE,WAAW;EACX,eAAe;EACf,YAAY;EACZ,wCAAwC;AAC1C","sourcesContent":[".add-todo-form {\n  border: 1px solid black; \n}\n\n.todo-item {\n  border-bottom: 1px solid rgb(180, 180, 180); \n  padding: 10px 0px; \n}\n\n.add-todo-button {\n  display: flex; \n  align-items: center; \n  gap: 10px; \n  width: 100%; \n  height: 45px; \n  background: none; \n  border: none; \n  color: rgb(150, 150, 150); \n  cursor: pointer; \n}\n\n.add-todo-button:hover {\n  color: var(--dark-color); \n}\n\n.add-todo-button img {\n  width: 20px; \n  height: 20px; \n  border-radius: 50%;\n  filter: brightness(0) saturate(100%) invert(62%) sepia(16%) saturate(18%) hue-rotate(338deg) brightness(94%) contrast(89%);\n}\n\n.add-todo-button .plus-icon {\n  width: