  --sidebar-width: 250px; 
  --dark-color-font: #1c2227; 
  --dark-color: #3b4953; 
  --semi-dark-color: #5A7863;
  --semi-light-color: #90Ab8B; 
  --light-color-shade: #cfdeb9; 
  --light-color: #ebf4dd; 
  --black-icon-light-filter: brightness(0) saturate(100%) invert(92%) sepia(4%) saturate(1172%) hue-rotate(357deg) brightness(110%) contrast(91%);
  --black-icon-semi-light-filter: brightness(0) saturate(100%) invert(74%) sepia(20%) saturate(361%) hue-rotate(64deg) brightness(85%) contrast(86%);
