.app-page {
  display: flex; 
  font-family: var(--font-family); 
}

.sidebar {
  width: var(--sidebar-width); 
  background-color: var(--light-color);
  height: 100vh; 
  display: flex; 
  position: sticky; 
  top: 0; 
  flex-direction: column; 
  padding: 30px 15px; 
}

.sidebar-header {
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
}

.sidebar-title {
  color: var(--dark-color); 
  margin: 0px; 
}

.add-project {
  background-color: transparent; 
  width: 30px; 
  height: 30px; 
  border: none; 
  cursor: pointer; 
  padding: 0px; 
  border-radius: 50%;
}

.add-project:hover {
  background-color: var(--dark-color); 
}

.add-project img {
  width: 30px; 
  height: 30px; 
  filter: var(--black-icon-dark-filter); 
}

.add-project:hover img {
  filter: var(--black-icon-light-filter); 
}

.projects {
  padding: 0px; 
}

.project-li {
  display: flex; 
  justify-content: space-between;
  list-style: none; 
  border-radius: 5px; 
  cursor: pointer; 
}

.select-project, 
input.rename-input, 
