  border-radius: 5px; 
  background-color: var(--light-color-shade); 
}

.project-li:hover {
  background-color: var(--light-color-shade); 
}

.projects .options {
  display: none; 
  padding-right: 15px; 
}

.project-li:hover > .options {
  display: flex; 
  align-items: center; 
  gap: 5px; 
}

.project-li .options button {
  background-color: transparent; 
  border: none; 
